<template>
  <div class="table-max mx-auto px-md-3 table-scroll">
    <div class="w-md-75">
      <h1 class="fs-4 text-center py-2">User Scores</h1>
    </div>
    <div class="table-container table-scroll w-100">
      
      <div class="my-4 px-2" v-for="(user, index) in data" :key="index">
        <h2 class="mb-0 fw-lighter text-center fs-4">{{user.email}}</h2>

        <div class="d-flex bg-secondary text-white text-center">
          <div class="col d-flex">
            <div class="col border border-white">Play TIme</div>
            <div class="col border border-white">High Score</div>
          </div>
          <div class="col-8 d-flex">
            <div class="dateColumn border border-white">Dates</div>
            <div class="scoresColumn flex-grow-1 border border-white">Scores</div>
          </div>
        </div><!-- table header -->
        <div class="d-flex py-1">
          <div class="col d-flex">
            <div class="col my-auto text-center">{{user.gameStats.totalUsageTime}}</div>
            <div class="col my-auto text-center">{{user.gameStats.highScore}}</div>
          </div>
          <div class="col-8">
            <div class="d-flex py-2" v-for="(day, index) in user.gameStats.days" :key="index">
              <div class="dateColumn fs-7 text-center">{{day.date}}</div>
              <div class="d-flex table-scroll  flex-grow-1 ">
                <div v-for="(score, index) in day.scores" :key="index" class="mx-2 fs-7">
                  {{score}}
                </div>
              </div>
            </div>
          </div>
        </div><!-- table body -->
      </div><!-- outmost for loop ends here -->
    </div><!-- table ends here -->
  </div><!-- root ends here -->
</template>

<script setup>
  import {onMounted,  ref} from 'vue'
  import {mapActions} from '@/libs/vuex'
  const {fetchUserScores} = mapActions()

  let data = ref(null)

  onMounted(async()=> {
    data.value = await fetchUserScores()
    data.value.forEach(user => user.gameStats.totalUsageTime = `${Math.floor(user.gameStats.totalUsageTime / 60000)} minutes`)
  })

</script>

<style scoped>
  .dateColumn{
    width: 140px;
  }

  .scoresColumn{
    width: 220px;
  } 


  .table-container {
    max-height: 75vh;
    min-width: 700px;
    overflow-y: auto;
    display:inline-block;
  }

  .table-max{
    max-width: 90vw;
  }

  @media (min-width:768px){
    .table-max{
      max-width: 65vw;
    }
  }

  @media (min-width:992px){
    .table-max{
      max-width: 75vw;
    }
  }

  
  .table-scroll{
    overflow: auto;
  }
  
  .table-scroll::-webkit-scrollbar {
    height: 7px;
    width: 10px;
  }
  /* Track */
  .table-scroll::-webkit-scrollbar-track {
    background: rgb(187, 185, 185); ;
  }
  /* Handle*/
  .table-scroll::-webkit-scrollbar-thumb, .content-box::-webkit-scrollbar-thumb:hover {
    background: var(--bs-primary);
  }

</style>